import React, { useContext } from "react"
import { List, ListItem, ListItemIcon, ListItemText } from "@mui/material"
import {
  ShoppingCart,
  AccountCircle,
  ExitToApp,
  Person,
  House,
  ViewList,
  AddBox,
} from "@mui/icons-material"
import { StateContext } from "../context/StateContext"

const DrawerContent = ({ navigate, toggleDrawer, token }) => {
  const auth = useContext(StateContext)
  return (
    <div style={{ width: 250 }}>
      <List>
        <ListItem
          button
          onClick={() => {
            navigate("/")
            toggleDrawer()
          }}
        >
          <ListItemIcon>
            <House />
          </ListItemIcon>
          <ListItemText primary="Home" />
        </ListItem>
        <ListItem
          button
          onClick={() => {
            navigate("/cart")
            toggleDrawer()
          }}
        >
          <ListItemIcon>
            <ShoppingCart />
          </ListItemIcon>
          <ListItemText primary="Cart" />
        </ListItem>
        {token ? (
          <>
            <ListItem
              button
              onClick={() => {
                navigate("/profile")
                toggleDrawer()
              }}
            >
              <ListItemIcon>
                <AccountCircle />
              </ListItemIcon>
              <ListItemText primary="My Profile" />
            </ListItem>
            <ListItem
              button
              onClick={() => {
                navigate("/my-order")
                toggleDrawer()
              }}
            >
              <ListItemIcon>
                <ViewList />
              </ListItemIcon>
              <ListItemText primary="My Orders" />
            </ListItem>
            <ListItem
              button
              onClick={() => {
                navigate("/my-product")
                toggleDrawer()
              }}
            >
              <ListItemIcon>
                <ViewList />
              </ListItemIcon>
              <ListItemText primary="My Products" />
            </ListItem>
            <ListItem
              button
              onClick={() => {
                navigate("/add-edit-product")
                toggleDrawer()
              }}
            >
              <ListItemIcon>
                <AddBox />
              </ListItemIcon>
              <ListItemText primary="Add Product" />
            </ListItem>
            <ListItem
              button
              onClick={() => {
                navigate("/")
                auth.logout()
                toggleDrawer()
              }}
            >
              <ListItemIcon>
                <ExitToApp />
              </ListItemIcon>
              <ListItemText primary="Logout" />
            </ListItem>
          </>
        ) : (
          <ListItem
            button
            onClick={() => {
              navigate("/login")
              toggleDrawer()
            }}
          >
            <ListItemIcon>
              <Person />
            </ListItemIcon>
            <ListItemText primary="Sign In" />
          </ListItem>
        )}
      </List>
    </div>
  )
}

export default DrawerContent
