// PayPalButton.js

import React, { useEffect, useCallback } from "react"
const PayPalButton = ({ value, updatedToPaid }) => {
  const initializePayPalButton = useCallback(() => {
    window.paypal
      .Buttons({
        createOrder: (data, actions) =>
          actions.order.create({
            purchase_units: [{ amount: { value } }],
          }),
        onApprove: (data, actions) =>
          actions.order.capture().then((details) => updatedToPaid(details)),
      })
      .render("#paypal-button-container")
  }, [])

  useEffect(() => {
    const script = document.createElement("script")
    script.src = `https://www.paypal.com/sdk/js?client-id=AWXyhqaou42l4s8zErt5_Us_lqqa6ZgxW4qSAG5DWtLfOvRqHDkVzJrWZKAa-oxwVXC5MPzA8rJmG2s8`
    script.async = true
    script.onload = initializePayPalButton

    document.body.appendChild(script)

    return () => {
      document.body.removeChild(script)
    }
  }, [initializePayPalButton])

  return <div id="paypal-button-container"></div>
}

export default PayPalButton
