import {
  Avatar,
  Box,
  Divider,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Typography,
  Paper,
} from "@mui/material"
import React from "react"

const OrderItem = ({ data }) => {
  const totalPrice = (data.qty * data.price).toFixed(2)

  return (
    <Box component={Paper} elevation={3} sx={{ mb: 2 }}>
      <ListItem>
        <ListItemAvatar>
          <Avatar
            variant="square"
            sx={{ width: 50, height: 50 }}
            src={"http://147.139.170.15:80" + data.image}
            alt={data.name}
          />
        </ListItemAvatar>
        <ListItemText
          primary={data.name}
          sx={{ ml: 2 }}
          secondary={
            <Typography variant="body2">
              {`${data.qty} x $${data.price} = $${totalPrice}`}
            </Typography>
          }
        />
      </ListItem>
      <Divider />
    </Box>
  )
}

export default OrderItem
