import ListProduct from "./product/ListProduct"
import DetailProduct from "./product/DetailProduct"
import { Route, Routes } from "react-router-dom"
import { Box, CssBaseline } from "@mui/material"
import { StateContext } from "./shared/context/StateContext"
import { useStateManagement } from "./shared/other/stateManagement"
import NavBar from "./shared/component/Navbar"
import { useEffect } from "react"
import ListCart from "./cart/ListCart"
import UserLogin from "./user/UserLogin"
import UserRegister from "./user/UserRegister"
import ShippingScreen from "./user/ShippingScreen"
import PaymentScreen from "./user/PaymentScreen"
import PlaceOrderScreen from "./user/PlaceOrderScreen"
import UserProfile from "./user/UserProfile"
import OrderScreen from "./user/OrderScreen"
import MyOrderScreen from "./user/MyOrderScreen"
import Footer from "./shared/component/Footer"
import ProductAddAndEditScreen from "./user/ProductAddAndEditScreen"
import MyProductScreen from "./user/MyProductScreen"
function App() {
  const {
    cartItems,
    token,
    userData,
    addCart,
    removeCart,
    initCart,
    login,
    logout,
    autoLogin,
    updateUser,
  } = useStateManagement()
  useEffect(() => {
    autoLogin()
    initCart()
  }, [initCart, autoLogin])

  return (
    <StateContext.Provider
      value={{
        cartItems,
        token,
        userData,
        addCart,
        removeCart,
        initCart,
        login,
        logout,
        autoLogin,
        updateUser,
      }}
    >
      <CssBaseline />
      <NavBar />
      <Box sx={{ minHeight: "80vh" }}>
        <Routes>
          <Route path="/" element={<ListProduct />} />
          <Route path="/detail-product" element={<DetailProduct />} />
          <Route path="/cart" element={<ListCart />} />
          <Route path="/login" element={<UserLogin />} />
          <Route path="/register" element={<UserRegister />} />
          <Route path="/shipping" element={<ShippingScreen />} />
          <Route path="/payment" element={<PaymentScreen />} />
          <Route path="/place-order" element={<PlaceOrderScreen />} />
          <Route path="/profile" element={<UserProfile />} />
          <Route path="/order/:id" element={<OrderScreen />} />
          <Route path="/my-order" element={<MyOrderScreen />} />
          <Route path="/my-product" element={<MyProductScreen />} />
          <Route path="/add-edit-product" element={<ProductAddAndEditScreen />} />
        </Routes>
      </Box>
      <Footer />
    </StateContext.Provider>
  )
}

export default App
