import React, { useContext } from "react"
import { StateContext } from "../../shared/context/StateContext"
import {
  Avatar,
  Box,
  IconButton,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Typography,
} from "@mui/material"
import { Delete } from "@mui/icons-material"

const CartItem = ({ data }) => {
  const state = useContext(StateContext)

  return (
    <Box sx={{ borderBottom: "1px solid #ddd" }}>
      <ListItem
        secondaryAction={
          <IconButton
            edge="end"
            aria-label="delete"
            onClick={() => {
              state.removeCart(data.name)
            }}
          >
            <Delete />
          </IconButton>
        }
      >
        <ListItemAvatar>
          <Avatar
            variant="square"
            sx={{ width: 80, height: 80, marginRight: 2 }}
            src={"http://147.139.170.15:80" + data.image}
            alt={data.name}
          />
        </ListItemAvatar>
        <ListItemText
          primary={
            <Typography variant="h6" fontWeight="bold">
              {data.name}
            </Typography>
          }
          secondary={
            <Typography>
              <Box component="span" fontWeight="bold" fontSize={16}>
                Price:
              </Box>{" "}
              ${data.price}
              <br />
              <Box component="span" fontWeight="bold" fontSize={16}>
                Quantity:
              </Box>{" "}
              {data.qty}
            </Typography>
          }
        />
      </ListItem>
    </Box>
  )
}

export default CartItem
