import React, { useContext, useEffect, useState } from "react"
import {
  Grid,
  Typography,
  Table,
  TableContainer,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  Button,
  CircularProgress,
  Alert,
} from "@mui/material"
import { Link } from "react-router-dom"
import { StateContext } from "../shared/context/StateContext"

const MyOrderScreen = () => {
  const auth = useContext(StateContext)
  const [err, setErr] = useState(null)
  const [orders, setOrder] = useState([])
  const [loading, setLoading] = useState(true)
  useEffect(() => {
    if (!auth.token) return
    const fetchData = async () => {
      try {
        const response = await fetch(`http://147.139.170.15:80/api/orders`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: "Bearer " + auth.token,
          },
        })
        if (!response.ok) {
          let value = await response.text()
          setErr(value)
          setLoading(false)
          return
        }
        let data = await response.json()
        setOrder(data)
        setLoading(false)
      } catch (error) {
        console.log(error)
      }
    }
    fetchData()
  }, [auth.token])

  return loading ? (
    <CircularProgress></CircularProgress>
  ) : err ? (
    <Alert severity="error">{err}</Alert>
  ) : (
    <Grid item md={9}>
      <Typography variant="h4">My Orders</Typography>
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell>DATE</TableCell>
              <TableCell>TOTAL</TableCell>
              <TableCell>PAID</TableCell>
              <TableCell>DELIVERED</TableCell>
              <TableCell>STATUS</TableCell>
              <TableCell>DETAILS</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {orders.map((order) => (
              <TableRow key={order._id}>
                <TableCell>{order._id}</TableCell>
                <TableCell>{order.createdAt.substring(0, 10)}</TableCell>
                <TableCell>{order.totalPrice}</TableCell>
                <TableCell>
                  {order.isPaid ? (
                    order.paymentResult.update_time.substring(0, 10)
                  ) : (
                    <span style={{ color: "red" }}>Unpaid</span>
                  )}
                </TableCell>
                <TableCell>
                  {order.isDelivered ? (
                    order.deliveredAt.substring(0, 10)
                  ) : (
                    <span style={{ color: "red" }}>Undelivered</span>
                  )}
                </TableCell>
                <TableCell>No</TableCell>
                <TableCell>
                  <Button variant="outlined" component={Link} to={`/order/${order._id}`}>
                    Details
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Grid>
  )
}

export default MyOrderScreen
