import React from "react"
import { Link } from "react-router-dom"
import { Box, Breadcrumbs, Link as MuiLink } from "@mui/material"
import ArrowForwardIcon from "@mui/icons-material/ArrowForward"

const CheckOutSteps = ({ step1, step2, step3, step4 }) => {
  return (
    <Box sx={{ display: "flex", justifyContent: "center", my: 2 }}>
      <Breadcrumbs separator={<ArrowForwardIcon fontSize="small" />} aria-label="breadcrumb">
        <StepLink to="/login" active={step1}>
          Login
        </StepLink>
        <StepLink to="/shipping" active={step2}>
          Shipping
        </StepLink>
        <StepLink to="/payment" active={step3}>
          Payment
        </StepLink>
        <StepLink to="/place-order" active={step4}>
          Place Order
        </StepLink>
      </Breadcrumbs>
    </Box>
  )
}

const StepLink = ({ to, active, children }) => (
  <MuiLink
    underline={active ? "none" : "hover"}
    color={active ? "primary.main" : "text.primary"}
    component={Link}
    to={to}
    sx={{
      fontSize: { xs: 14, md: 18 },
      textDecoration: "none",
      fontWeight: active ? "bold" : "normal",
    }}
  >
    {children}
  </MuiLink>
)

export default CheckOutSteps
