import React, { useContext, useState } from "react"
import {
  Button,
  TextField,
  Typography,
  Container,
  Grid,
  Paper,
  Alert,
  LinearProgress,
} from "@mui/material"
import { StateContext } from "../shared/context/StateContext"
import { useLocation, useNavigate } from "react-router-dom"
import { CloudUpload } from "@mui/icons-material"
import { validationProductAddEndEdit } from "../shared/other/validationTextField"

const ProductAddAndEditScreen = () => {
  const auth = useContext(StateContext)
  const [errMessage, setErrMessage] = useState(null)
  const navigate = useNavigate()
  let { state } = useLocation()
  let data = {
    name: "",
    description: "",
    brand: "",
    category: "",
    price: 0,
    countInStock: 0,
  }
  if (state) {
    for (let key in data) {
      data[key] = state[key]
    }
  }
  let [formDataObj, setFormDataObj] = useState(data)

  const [validation, setValidation] = useState({
    name: { error: false, message: "" },
    description: { error: false, message: "" },
    brand: { error: false, message: "" },
    category: { error: false, message: "" },
    price: { error: false, message: "" },
    countInStock: { error: false, message: "" },
  })

  const [file, setFile] = useState(null)
  const [uploading, setUploading] = useState(false)

  const handleChange = (e) => {
    const { name, value } = e.target
    setFormDataObj((prevData) => ({ ...prevData, [name]: value }))
  }

  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0]
    setFile(selectedFile)
    setErrMessage(null)
  }

  const submitHandler = async (e) => {
    e.preventDefault()
    const { updatedValue, errorValidate } = validationProductAddEndEdit(
      formDataObj.name,
      formDataObj.description,
      formDataObj.brand,
      formDataObj.category,
      formDataObj.price,
      formDataObj.countInStock
    )
    if (errorValidate) {
      setValidation(updatedValue)
    } else {
      setValidation(updatedValue)
      setUploading(true)
      const formData = new FormData()
      for (const key in formDataObj) {
        formData.append(key, formDataObj[key])
      }
      formData.append("file", file)
      if (!state) {
        if (!file) {
          setErrMessage("file is not found")
          return
        }
        try {
          const response = await fetch("http://147.139.170.15:80/api/products", {
            method: "POST",
            headers: {
              Authorization: "Bearer " + auth.token,
            },
            body: formData,
          })
          if (!response.ok) {
            const text = await response.text()
            setErrMessage(text)
            return
          }
          navigate("/")
        } catch (error) {
          console.log(error)
        }
        setUploading(false)
      } else {
        try {
          const response = await fetch(
            `http://147.139.170.15:80/api/products?userId=${auth.userData._id}&productId=${state._id}`,
            {
              method: "PUT",
              headers: {
                Authorization: "Bearer " + auth.token,
              },
              body: formData,
            }
          )
          if (!response.ok) {
            return
          }
          navigate("/")
        } catch (error) {
          console.log(error)
        }
        setUploading(false)
      }
    }
  }

  return (
    <Container component="main" maxWidth="md">
      <Paper elevation={3} style={{ padding: 20, marginTop: 20 }}>
        <Typography variant="h4" align="center" gutterBottom>
          {state ? "Edit Product" : "Add Product"}
        </Typography>
        <form onSubmit={submitHandler}>
          <Grid container spacing={3}>
            {Object.entries(formDataObj).map(([key, value]) => (
              <Grid item xs={12} key={key}>
                <TextField
                  fullWidth
                  label={key}
                  variant="outlined"
                  name={key}
                  error={validation[key].error}
                  helperText={validation[key].message}
                  value={value}
                  multiline={key === "description"}
                  rows={key === "description" ? 3 : 1}
                  onChange={handleChange}
                  type={
                    key === "price" ||
                    key === "countInStock" ||
                    key === "rating" ||
                    key === "numReviews"
                      ? "number"
                      : "text"
                  }
                />
              </Grid>
            ))}

            <Grid item xs={12}>
              <label htmlFor="image-input" style={{ display: "flex", alignItems: "center" }}>
                <input
                  type="file"
                  accept="image/*"
                  style={{ display: "none" }}
                  id="image-input"
                  onChange={handleFileChange}
                />
                <Button
                  variant="outlined"
                  color="primary"
                  component="span"
                  startIcon={<CloudUpload />}
                  style={{ marginLeft: 10 }}
                >
                  Upload Image
                </Button>
                {file && (
                  <Typography variant="body1" color="textSecondary" style={{ marginLeft: 10 }}>
                    {file.name}
                  </Typography>
                )}
              </label>
              {errMessage && (
                <Alert severity="error" sx={{ width: "100%", mt: 2, textAlign: "center" }}>
                  {errMessage}
                </Alert>
              )}
            </Grid>

            {uploading && (
              <Grid item xs={12}>
                <LinearProgress color="primary" />
              </Grid>
            )}

            <Grid item xs={12}>
              <Button type="submit" variant="contained" color="primary" fullWidth>
                {uploading ? "Uploading..." : "Submit"}
              </Button>
            </Grid>
          </Grid>
        </form>
      </Paper>
    </Container>
  )
}

export default ProductAddAndEditScreen
