import {
  Box,
  Button,
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  Typography,
  Container,
} from "@mui/material"
import React, { useState } from "react"
import { useNavigate } from "react-router-dom"
import CheckOutSteps from "./compenent/CheckOutSteps"

const PaymentScreen = () => {
  const navigate = useNavigate()
  const [paymentMethod, setPaymentMethod] = useState("PayPal")

  const handleCheckboxChange = (event) => {
    setPaymentMethod(event.target.checked ? "PayPal" : "")
  }

  const submitHandler = () => {
    localStorage.setItem("paymentMethod", paymentMethod)
    navigate("/place-order")
  }

  return (
    <Container maxWidth="xl" sx={{ paddingY: 4 }}>
      <CheckOutSteps step1 step2 step3 />
      <Typography variant="h4" align="center" mt={4} mb={2}>
        Choose Payment Method
      </Typography>
      <Box display="flex" justifyContent="center">
        <FormControl component="fieldset">
          <FormGroup>
            <FormControlLabel
              control={
                <Checkbox
                  checked={paymentMethod === "PayPal"}
                  onChange={handleCheckboxChange}
                  value="PayPal"
                />
              }
              label="Paypal or Credit Card"
            />
            <FormControlLabel disabled control={<Checkbox />} label="Disabled" />
          </FormGroup>
        </FormControl>
      </Box>
      <Box textAlign="center" mt={4}>
        <Button variant="contained" color="primary" size="large" onClick={submitHandler}>
          Continue
        </Button>
      </Box>
    </Container>
  )
}

export default PaymentScreen
