import React, { useContext, useState } from "react"
import {
  AppBar,
  Button,
  Drawer,
  IconButton,
  Toolbar,
  Typography,
  Menu,
  MenuItem,
} from "@mui/material"
import { Person, ShoppingCart, Menu as MenuIcon, AccountCircle } from "@mui/icons-material"
import { blue } from "@mui/material/colors"
import { Link, useNavigate } from "react-router-dom"
import DrawerContent from "./DrawerContent"
import { StateContext } from "../context/StateContext"

const Navbar = () => {
  const { token, userData, logout } = useContext(StateContext)
  const [anchorEl, setAnchorEl] = useState(null)
  const [isDrawerOpen, setDrawerOpen] = useState(false)

  const handleOpen = (e) => {
    setAnchorEl(e.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const toggleDrawer = () => {
    setDrawerOpen(!isDrawerOpen)
  }

  const navigate = useNavigate()

  const appBarStyles = {
    position: "sticky",
    background: blue[500],
  }

  const buttonStyles = {
    display: { xs: "none", md: "flex" },
    alignItems: "center",
  }

  const profileButtonStyles = {
    backgroundColor: "white",
    textTransform: "none",
    borderRadius: "20px",
    padding: "10px",
    marginRight: "10px",
    transition: "background-color 0.3s ease", // Adding a transition for a smoother effect
    "&:hover": {
      backgroundColor: "#f0f0f0", // Change to your preferred hover color
    },
  }

  const iconStyles = {
    fontSize: "24px",
    marginRight: "5px",
  }

  return (
    <>
      <AppBar position="sticky" sx={appBarStyles}>
        <Toolbar>
          <IconButton
            color="inherit"
            edge="start"
            onClick={toggleDrawer}
            sx={{ marginRight: 2, display: { sm: "block", md: "none" } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            variant="h5"
            component={Link}
            to="/"
            sx={{
              flexGrow: 1,
              fontWeight: "bold",
              textTransform: "uppercase",
              textDecoration: "none",
              color: "white",
            }}
          >
            Go Shop
          </Typography>
          <Button
            color="inherit"
            onClick={() => {
              navigate("/cart")
            }}
            sx={buttonStyles}
          >
            <ShoppingCart sx={{ mr: 1 }} />
            <Typography variant="body1" sx={{ fontWeight: "bold" }}>
              Cart
            </Typography>
          </Button>
          {token ? (
            <>
              <Button color="primary" onClick={handleOpen} sx={profileButtonStyles}>
                <AccountCircle sx={iconStyles} />
                <Typography
                  variant="h6"
                  sx={{ fontWeight: "bold", display: { xs: "none", md: "block" } }}
                >
                  {userData.name}
                </Typography>
              </Button>
              <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={handleClose}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "right",
                }}
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
              >
                <MenuItem
                  onClick={() => {
                    navigate("/profile")
                    handleClose()
                  }}
                  sx={{ fontSize: "16px" }}
                >
                  My Profile
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    navigate("/my-order")
                    handleClose()
                  }}
                  sx={{ fontSize: "16px" }}
                >
                  My Orders
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    navigate("/my-product")
                    handleClose()
                  }}
                  sx={{ fontSize: "16px" }}
                >
                  My Products
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    navigate("/add-edit-product")
                    handleClose()
                  }}
                  sx={{ fontSize: "16px" }}
                >
                  Add Product
                </MenuItem>

                <MenuItem
                  onClick={() => {
                    navigate("/login")
                    logout()
                    handleClose()
                  }}
                  sx={{ fontSize: "16px" }}
                >
                  Logout
                </MenuItem>
              </Menu>
            </>
          ) : (
            <Button
              color="inherit"
              onClick={() => {
                navigate("/login")
              }}
              sx={buttonStyles}
            >
              <Person sx={{ mr: 1 }} />
              <Typography variant="body1" sx={{ fontWeight: "bold" }}>
                Sign In
              </Typography>
            </Button>
          )}
        </Toolbar>
      </AppBar>
      <Drawer anchor="left" open={isDrawerOpen} onClose={toggleDrawer}>
        <DrawerContent navigate={navigate} toggleDrawer={toggleDrawer} token={token} />
      </Drawer>
    </>
  )
}

export default Navbar
