import React, { useContext } from "react"
import { useNavigate } from "react-router-dom"
import { Box, Button, CircularProgress, Container, Divider, Grid, Typography } from "@mui/material"
import { styled } from "@mui/system"
import CheckOutSteps from "./compenent/CheckOutSteps"
import OrderItem from "./compenent/OrderItem"
import { StateContext } from "../shared/context/StateContext"

const StyledButton = styled(Button)({
  width: "100%",
  padding: "12px 24px",
  borderRadius: "8px",
  fontWeight: "bold",
  textTransform: "uppercase",
  backgroundColor: "#1976D2",
  color: "#fff",
  "&:hover": {
    backgroundColor: "#1565c0",
  },
})

const PlaceOrderScreen = () => {
  const navigate = useNavigate()
  const auth = useContext(StateContext)
  const shipping = JSON.parse(localStorage.getItem("shipping"))
  const carts = JSON.parse(localStorage.getItem("cartItems"))
  const paymentMethod = localStorage.getItem("paymentMethod")
  const isLoading = false // Set this dynamically based on your loading state

  const totalPrice = carts.reduce((acc, item) => acc + item.price * item.qty, 0).toFixed(2) // Fix total price to two decimal places
  const shippingPrice = totalPrice > 100 ? 0 : 100
  const tax = (0.15 * totalPrice).toFixed(2)
  const total = (Number(totalPrice) + Number(shippingPrice) + Number(tax)).toFixed(2)

  const submitHandler = async () => {
    try {
      let body = {
        user: auth.userData._id,
        orderItems: carts,
        paymentMethod,
        shippingPrice,
        taxPrice: tax,
        totalPrice: total,
        shippingAddress: shipping,
        isPaid: false,
        isDelivered: false,
      }
      let response = await fetch("http://147.139.170.15:80/api/orders", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + auth.token,
          // 'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: JSON.stringify(body),
      })
      let data = await response.json()
      navigate(`/order/${data._id}`)
    } catch (error) {
      // Handle the error
    }
  }

  return (
    <Container maxWidth="xl" sx={{ paddingY: 4 }}>
      <CheckOutSteps step1 step2 step3 step4 />

      <Grid container spacing={4}>
        <>
          {/* Shipping, Payment Method, and Order Item */}
          <Grid item xs={12} md={8}>
            <Box sx={{ p: 2, border: "1px solid", borderRadius: "8px" }}>
              <Typography variant="h6">Shipping</Typography>
              <Typography variant="body1">
                Address: {shipping.address}, {shipping.city}, {shipping.postalCode},{" "}
                {shipping.country}
              </Typography>
              <Divider sx={{ mt: 2, mb: 2 }} />
              <Typography variant="h6">Payment Method</Typography>
              <Typography variant="body1"> Method: {paymentMethod}</Typography>
              <Divider sx={{ mt: 2, mb: 2 }} />
              <Typography variant="h6">Order Item</Typography>
              {carts.map((data) => (
                <OrderItem key={data._id} data={data} />
              ))}
            </Box>
          </Grid>
          {/* ORDER SUMMARY for large screens on the left */}
          <Grid item xs={12} md={4}>
            <Box sx={{ p: 2, border: "1px solid", borderRadius: "8px" }}>
              <Typography variant="h6" fontWeight="bold" textAlign="center" marginBottom={2}>
                ORDER SUMMARY
              </Typography>
              <Divider sx={{ mt: 2, mb: 2 }} />
              <Grid container justifyContent="space-between" alignItems="center">
                <Grid item>
                  <Typography variant="subtitle1" color="textSecondary">
                    Items:
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography variant="body2" color="textSecondary">
                    ${totalPrice}
                  </Typography>
                </Grid>
              </Grid>
              <Divider sx={{ mt: 2, mb: 2 }} />
              <Grid container justifyContent="space-between" alignItems="center">
                <Grid item>
                  <Typography variant="subtitle1" color="textSecondary">
                    Shipping:
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography variant="body2" color="textSecondary">
                    ${shippingPrice}
                  </Typography>
                </Grid>
              </Grid>
              <Divider sx={{ mt: 2, mb: 2 }} />
              <Grid container justifyContent="space-between" alignItems="center">
                <Grid item>
                  <Typography variant="subtitle1" color="textSecondary">
                    Tax:
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography variant="body2" color="textSecondary">
                    ${tax}
                  </Typography>
                </Grid>
              </Grid>
              <Divider sx={{ mt: 2, mb: 2 }} />
              <Grid container justifyContent="space-between" alignItems="center">
                <Grid item>
                  <Typography variant="subtitle1" color="textSecondary">
                    Total:
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography variant="body2" color="textSecondary">
                    ${total}
                  </Typography>
                </Grid>
              </Grid>
              <Divider sx={{ mt: 2, mb: 2 }} />
              <StyledButton onClick={submitHandler} disabled={isLoading}>
                {isLoading ? <CircularProgress color="inherit" size={30} /> : "Place Order"}
              </StyledButton>
            </Box>
          </Grid>
        </>
      </Grid>
    </Container>
  )
}

export default PlaceOrderScreen
