import { Button, Container, TextField, Typography, Grid, InputLabel } from "@mui/material"
import React, { useEffect, useState } from "react"
import CheckOutSteps from "./compenent/CheckOutSteps"
import { validationShipping } from "../shared/other/validationTextField"
import { useNavigate } from "react-router-dom"

const ShippingScreen = () => {
  const [formData, setFormData] = useState({
    address: "",
    city: "",
    postalCode: "",
    country: "",
  })

  const [validation, setValidation] = useState({
    address: { error: false, message: "" },
    city: { error: false, message: "" },
    postalCode: { error: false, message: "" },
    country: { error: false, message: "" },
  })

  useEffect(() => {
    const data = JSON.parse(localStorage.getItem("shipping"))
    if (data) {
      setFormData(data)
    }
  }, [])

  const navigate = useNavigate()

  const handleInputChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
  }

  const submitHandler = () => {
    const { errorValidate, updatedValue } = validationShipping(
      formData.address,
      formData.city,
      formData.postalCode,
      formData.country
    )

    if (errorValidate) {
      setValidation(updatedValue)
    } else {
      localStorage.setItem("shipping", JSON.stringify(formData))
      navigate("/payment")
    }
  }

  return (
    <Container maxWidth="xl" sx={{ paddingY: 4 }}>
      <CheckOutSteps step1 step2 />
      <Container maxWidth="xs">
        <Grid container spacing={2}>
          {["address", "city", "postalCode", "country"].map((field) => (
            <Grid item xs={12} key={field}>
              <InputLabel htmlFor={field}>
                <Typography variant="h6">
                  {field.charAt(0).toUpperCase() + field.slice(1)}
                </Typography>
              </InputLabel>
              <TextField
                fullWidth
                id={field}
                name={field}
                type={field === "postalCode" ? "number" : "text"}
                error={validation[field].error}
                helperText={validation[field].message}
                value={formData[field]}
                onChange={handleInputChange}
              />
            </Grid>
          ))}
        </Grid>
        <Button
          variant="contained"
          color="primary"
          type="submit"
          sx={{ alignSelf: "center", mt: 2, width: "100%" }}
          onClick={submitHandler}
        >
          Continue
        </Button>
      </Container>
    </Container>
  )
}

export default ShippingScreen
