import { Box, CircularProgress, Container, Divider, Grid, Typography, Alert } from "@mui/material"
import React, { useContext, useEffect, useState } from "react"
import { useParams } from "react-router-dom"

import OrderItem from "./compenent/OrderItem"
import { StateContext } from "../shared/context/StateContext"
import PayPalButton from "../shared/component/PayPalButton"

const OrderScreen = () => {
  const [data, setData] = useState({})
  const auth = useContext(StateContext)
  var updatedToPaid = async (details) => {
    data.isPaid = true
    data.paymentResult = {
      id: details.id,
      status: details.status,
      update_time: details.update_time,
      payer: details.payer.email_address,
    }
    const response = await fetch(`http://147.139.170.15:80/api/orders`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + auth.token,
      },
      body: JSON.stringify(data),
    })
    let resData = await response.json()
    console.log(resData)
    setData(resData)
  }

  const params = useParams()

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(`http://147.139.170.15:80/api/orders?id=${params.id}`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: "Bearer " + auth.token,
          },
        })
        let data = await response.json()
        setData(data)
      } catch (error) {
        console.error("Error fetching order data:", error)
      }
    }
    fetchData()
  }, [params.id, auth.token])

  return (
    <Container maxWidth="xl" sx={{ paddingY: 2 }}>
      <Grid container spacing={4}>
        <Grid item xs={12} md={8}>
          <Box sx={{ p: 2, border: "1px solid", borderRadius: "8px" }}>
            <Typography variant="h6">Shipping</Typography>
            <Typography variant="body1">
              Address: {data.shippingAddress?.address}, {data.shippingAddress?.city},{" "}
              {data.shippingAddress?.postalCode}, {data.shippingAddress?.country}
            </Typography>
            <Divider sx={{ mt: 2, mb: 2 }} />
            <Typography variant="h6">Is Paid</Typography>
            {data.isPaid ? (
              <Alert severity="success">
                Order is success paid at {data.paymentResult.update_time}
              </Alert>
            ) : (
              <Alert severity="warning">Order is not paid</Alert>
            )}
            <Divider sx={{ mt: 2, mb: 2 }} />
            <Typography variant="h6">Payment Method</Typography>
            <Typography variant="body1"> Method: {data.paymentMethod}</Typography>
            <Divider sx={{ mt: 2, mb: 2 }} />
            <Typography variant="h6">Is Delivered</Typography>
            {data.isDelivered ? (
              <Alert severity="success">Order is success delivered</Alert>
            ) : (
              <Alert severity="warning">Order is not delivered</Alert>
            )}
            <Divider sx={{ mt: 2, mb: 2 }} />
            <Typography variant="h6">Order Item</Typography>
            {data.orderItems?.map((orderItem) => (
              <OrderItem key={orderItem._id} data={orderItem} />
            ))}
          </Box>
        </Grid>
        <Grid item xs={12} md={4}>
          <Box sx={{ p: 2, border: "1px solid", borderRadius: "8px" }}>
            <Typography variant="h6" fontWeight="bold" textAlign="center" marginBottom={2}>
              ORDER SUMMARY
            </Typography>
            <Divider sx={{ mt: 2, mb: 2 }} />
            <Grid container justifyContent="space-between" alignItems="center">
              <Grid item>
                <Typography variant="subtitle1" color="textSecondary">
                  Items:
                </Typography>
              </Grid>
              <Grid item>
                <Typography variant="body2" color="textSecondary">
                  ${data.totalPrice}
                </Typography>
              </Grid>
            </Grid>
            <Divider sx={{ mt: 2, mb: 2 }} />
            <Grid container justifyContent="space-between" alignItems="center">
              <Grid item>
                <Typography variant="subtitle1" color="textSecondary">
                  Shipping:
                </Typography>
              </Grid>
              <Grid item>
                <Typography variant="body2" color="textSecondary">
                  ${data.shippingPrice}
                </Typography>
              </Grid>
            </Grid>
            <Divider sx={{ mt: 2, mb: 2 }} />
            <Grid container justifyContent="space-between" alignItems="center">
              <Grid item>
                <Typography variant="subtitle1" color="textSecondary">
                  Tax:
                </Typography>
              </Grid>
              <Grid item>
                <Typography variant="body2" color="textSecondary">
                  ${data.taxPrice}
                </Typography>
              </Grid>
            </Grid>
            <Divider sx={{ mt: 2, mb: 2 }} />
            <Grid container justifyContent="space-between" alignItems="center">
              <Grid item>
                <Typography variant="subtitle1" color="textSecondary">
                  Total:
                </Typography>
              </Grid>
              <Grid item>
                <Typography variant="body2" color="textSecondary">
                  ${data.totalPrice}
                </Typography>
              </Grid>
            </Grid>
            <Divider sx={{ mt: 2, mb: 2 }} />
            {data.totalPrice && !data.isPaid ? (
              <PayPalButton value={data.totalPrice} updatedToPaid={updatedToPaid} />
            ) : !data.isPaid ? (
              <CircularProgress />
            ) : (
              <Alert security="info">data has been pay</Alert>
            )}
          </Box>
        </Grid>
      </Grid>
    </Container>
  )
}

export default OrderScreen
