import React, { useContext, useEffect, useState } from "react"
import {
  Button,
  Card,
  CircularProgress,
  Container,
  CssBaseline,
  Stack,
  TextField,
  Typography,
  Alert,
  styled,
} from "@mui/material"
import { useNavigate, useSearchParams } from "react-router-dom"
import { validationLogin } from "../shared/other/validationTextField"
import { StateContext } from "../shared/context/StateContext"
import { Link } from "react-router-dom"

const StyledContainer = styled(Container)({
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  minHeight: "100vh",
})

const StyledCard = styled(Card)(({ theme }) => ({
  maxWidth: 400,
  width: "100%",
  padding: theme.spacing(4),
  borderRadius: theme.spacing(2),
  boxShadow: theme.shadows[4],
  textAlign: "center",
}))

const StyledButton = styled(Button)(({ theme }) => ({
  width: "100%",
  marginTop: theme.spacing(2),
}))

const StyledLink = styled(Link)(({ theme }) => ({
  marginTop: theme.spacing(1),
  fontWeight: "bold",
  textDecoration: "none",
  color: "blue",
  marginLeft: theme.spacing(1),
}))

const UserLogin = () => {
  const [isLoading, setIsLoading] = useState(false)
  const navigate = useNavigate()
  const auth = useContext(StateContext)
  const [searchParams] = useSearchParams()
  const [err, setErr] = useState(false)
  const [message, setMessage] = useState("")

  useEffect(() => {
    if (auth.token) {
      navigate("/")
    }
  }, [auth, navigate])

  const [validation, setValidation] = useState({
    email: {
      error: false,
      message: "",
    },
    password: {
      error: false,
      message: "",
    },
  })

  const submitHandler = async (e) => {
    e.preventDefault()
    const body = {
      email: document.getElementById("email").value,
      password: document.getElementById("password").value,
    }
    const { errorValidate, updatedValue } = validationLogin(body.email, body.password)

    if (errorValidate) {
      setValidation(updatedValue)
    } else {
      setValidation(updatedValue)
      setIsLoading(true)

      try {
        const user = await fetch("http://147.139.170.15:80/api/users?login=1", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            // 'Content-Type': 'application/x-www-form-urlencoded',
          },
          body: JSON.stringify(body),
        })
        if (!user.ok) {
          setErr(true)
          const message = await user.text()
          setMessage(message)
          return
        }
        const userData = await user.json()
        setIsLoading(true)
        userData.exp = new Date().getTime() + 1 * 60 * 60 * 1000

        auth.login(userData)
        const redirect = searchParams.get("redirect")

        if (redirect) {
          navigate("/" + redirect)
        } else {
          navigate("/")
        }
      } catch (error) {
        console.error("Login error:", error)
      } finally {
        setIsLoading(false)
      }
    }
  }

  return (
    <StyledContainer component="main" maxWidth="xs">
      <CssBaseline />
      <StyledCard>
        <Typography variant="h4" fontWeight="bold" sx={{ mb: 2 }}>
          Login
        </Typography>
        {err && (
          <Alert severity="error" sx={{ mb: 4 }}>
            {message}
          </Alert>
        )}
        <form onSubmit={submitHandler}>
          <Stack spacing={2}>
            <TextField
              type="email"
              fullWidth
              id="email"
              label="Email"
              error={validation.email.error}
              helperText={validation.email.message}
            />
            <TextField
              type="password"
              fullWidth
              id="password"
              label="Password"
              error={validation.password.error}
              helperText={validation.password.message}
            />
          </Stack>
          <StyledButton
            variant="contained"
            color="primary"
            type="submit"
            disabled={isLoading}
            sx={{ mt: 2 }}
          >
            {isLoading ? <CircularProgress color="inherit" size={20} /> : "Login"}
          </StyledButton>
        </form>
        <Typography variant="body2" sx={{ mt: 1 }}>
          Don't have an account?
          <StyledLink component={Link} to="/register">
            Register
          </StyledLink>
        </Typography>
      </StyledCard>
    </StyledContainer>
  )
}

export default UserLogin
