import React, { useState } from "react"
import {
  Button,
  Card,
  CircularProgress,
  Container,
  CssBaseline,
  Stack,
  TextField,
  Typography,
  styled,
  Alert,
} from "@mui/material"
import { useNavigate } from "react-router-dom"
import { validationRegister } from "../shared/other/validationTextField"
import { Link } from "react-router-dom"

const StyledContainer = styled(Container)({
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  minHeight: "100vh",
})

const StyledCard = styled(Card)(({ theme }) => ({
  maxWidth: 400,
  width: "100%",
  padding: theme.spacing(4),
  borderRadius: theme.spacing(2),
  boxShadow: theme.shadows[4],
  textAlign: "center",
}))

const StyledButton = styled(Button)(({ theme }) => ({
  width: "100%",
  marginTop: theme.spacing(2),
}))

const StyledLink = styled(Link)(({ theme }) => ({
  marginTop: theme.spacing(1),
  fontWeight: "bold",
  textDecoration: "none",
  color: "blue",
  marginLeft: theme.spacing(1),
}))

const UserRegister = () => {
  const [isLoading, setIsLoading] = useState(false)
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
  })
  const [validation, setValidation] = useState({
    name: { error: false, message: "" },
    email: { error: false, message: "" },
    password: { error: false, message: "" },
    confirmPassword: { error: false, message: "" },
  })
  const [err, setErr] = useState(false)
  const [message, setMessage] = useState("")

  const navigate = useNavigate()

  const handleChange = (e) => {
    const { id, value } = e.target
    setFormData((prevData) => ({ ...prevData, [id]: value }))
  }

  const submitHandler = async (e) => {
    e.preventDefault()
    const body = {
      name: document.getElementById("name").value,
      email: document.getElementById("email").value,
      password: document.getElementById("password").value,
      confirmPassword: document.getElementById("confirmPassword").value,
    }
    const { errorValidate, updatedValue } = validationRegister(
      body.name,
      body.email,
      body.password,
      body.confirmPassword
    )

    if (errorValidate) {
      setValidation(updatedValue)
    } else {
      setValidation(updatedValue)
      setIsLoading(true)

      try {
        let response = await fetch("http://147.139.170.15:80/api/users?login=0", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            // 'Content-Type': 'application/x-www-form-urlencoded',
          },
          body: JSON.stringify(body),
        })
        if (!response.ok) {
          setErr(true)
          let text = await response.text()
          setMessage(text)
          return
        }

        navigate("/login")
      } catch (error) {
        console.error("Login error:", error)
      } finally {
        setIsLoading(false)
      }
    }
  }

  return (
    <StyledContainer component="main" maxWidth="xs">
      <CssBaseline />
      <StyledCard>
        <Typography variant="h4" fontWeight="bold" sx={{ mb: 2 }}>
          Register
        </Typography>
        {err && (
          <Alert severity="error" sx={{ mb: 4 }}>
            {message}
          </Alert>
        )}
        <form onSubmit={submitHandler}>
          <Stack spacing={2}>
            <TextField
              type="text"
              fullWidth
              id="name"
              label="Name"
              value={formData.name}
              onChange={handleChange}
              error={validation.name.error}
              helperText={validation.name.message}
            />
            <TextField
              type="email"
              fullWidth
              id="email"
              label="Email"
              value={formData.email}
              onChange={handleChange}
              error={validation.email.error}
              helperText={validation.email.message}
            />
            <TextField
              type="password"
              fullWidth
              id="password"
              label="Password"
              value={formData.password}
              onChange={handleChange}
              error={validation.password.error}
              helperText={validation.password.message}
            />
            <TextField
              type="password"
              fullWidth
              id="confirmPassword"
              label="Confirm Password"
              value={formData.confirmPassword}
              onChange={handleChange}
              error={validation.confirmPassword.error}
              helperText={validation.confirmPassword.message}
            />
          </Stack>
          <StyledButton
            variant="contained"
            color="primary"
            type="submit"
            disabled={isLoading}
            sx={{ mt: 2 }}
          >
            {isLoading ? <CircularProgress color="inherit" size={20} /> : "Register"}
          </StyledButton>
        </form>
        <Typography variant="body2" sx={{ mt: 1 }}>
          Already have an account?
          <StyledLink component={Link} to="/login">
            Log In
          </StyledLink>
        </Typography>
      </StyledCard>
    </StyledContainer>
  )
}

export default UserRegister
