import React, { useEffect, useState } from "react"
import { Box, CircularProgress, Grid, useMediaQuery, useTheme } from "@mui/material"
import ProductItem from "./component/ProductItem"

const ListProduct = () => {
  const [products, setProducts] = useState(null)
  const [error, setError] = useState(null)
  const theme = useTheme()
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"))

  useEffect(() => {
    const fetchData = async () => {
      try {
        let data = await fetch("http://147.139.170.15:80/api/products")
        data = await data.json()
        setProducts(Object.values(data))
      } catch (error) {
        setError("Error fetching data. Please try again later.")
      }
    }
    fetchData()
  }, [])

  const style = {
    position: "fixed",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "100%",
    textAlign: "center",
  }

  return (
    <>
      {error ? (
        <Box sx={style}>
          <div>Error fetching data. Please try again later.</div>
        </Box>
      ) : !products ? (
        <Box sx={style}>
          <CircularProgress sx={{ mt: 4 }} thickness={7} size={60} />
        </Box>
      ) : (
        <Grid container spacing={isMobile ? 2 : 2}>
          {products.length === 0 ? (
            <Box sx={{ textAlign: "center", mt: 4 }}>
              <div>No products available.</div>
            </Box>
          ) : (
            products.map((el) => (
              <Grid item xs={12} sm={6} md={4} lg={3} key={el._id}>
                <ProductItem data={el} />
              </Grid>
            ))
          )}
        </Grid>
      )}
    </>
  )
}

export default ListProduct
