import React, { useContext, useEffect, useState } from "react"
import { useNavigate } from "react-router-dom"
import {
  Button,
  CircularProgress,
  Container,
  Grid,
  Paper,
  Alert,
  Typography,
  TextField,
} from "@mui/material"
import { validationUserUpdate } from "../shared/other/validationTextField"
import { StateContext } from "../shared/context/StateContext"

const UserProfile = () => {
  const [message, setMessage] = useState(null)
  const [err, setErr] = useState(null)
  const [validation, setValidation] = useState({
    email: { error: false, message: "" },
    password: { error: false, message: "" },
    name: { error: false, message: "" },
    confirmPassword: { error: false, message: "" },
  })
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
  })
  const [isLoading, setIsLoading] = useState(false)
  const navigate = useNavigate()
  const { userData, token, updateUser } = useContext(StateContext)

  useEffect(() => {
    if (!userData.token) {
      navigate("/login")
    } else {
      setFormData({
        name: userData.name,
        email: userData.email,
        password: "",
        confirmPassword: "",
      })
    }
  }, [token, userData, navigate, setFormData])

  const handleInputChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
  }

  const submitHandler = async (e) => {
    e.preventDefault()
    setIsLoading(true)

    const { errorValidate, updatedValue } = validationUserUpdate(
      formData.name,
      formData.email,
      formData.password,
      formData.confirmPassword
    )

    setValidation(updatedValue)

    if (!errorValidate) {
      try {
        // Simulate an asynchronous update operation (replace with actual API call)
        const updatedUserData = {
          name: formData.name,
          email: formData.email,
          password: formData.password,
        }
        const user = await fetch("http://147.139.170.15:80/api/users", {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
            Authorization: "Bearer " + token,
            // 'Content-Type': 'application/x-www-form-urlencoded',
          },
          body: JSON.stringify(updatedUserData),
        })
        if (!user.ok) {
          let text = await user.text()
          setErr(text)
          return
        }
        setErr(null)
        let data = await user.json()
        updateUser(data)
        setMessage("succes update user")

        setTimeout(() => {
          setMessage(null)
        }, 3000)
        // if (!user.ok) {
        //   setErr(true)
        //   const message = await user.text()
        //   setMessage(message)
        //   return
        // }
        // Provide feedback to the user on successful update
        // You can use Snackbar, Toast, or any other UI component for this purpose
      } catch (error) {
        // Handle error
      } finally {
        setIsLoading(false)
      }
    } else {
      setIsLoading(false)
    }
  }

  return (
    <Container component="main" maxWidth="md">
      <Paper
        elevation={3}
        sx={{ padding: 4, display: "flex", flexDirection: "column", alignItems: "center" }}
      >
        <Typography variant="h4" mb={3}>
          User Profile
        </Typography>
        <form onSubmit={submitHandler} style={{ width: "100%" }}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <TextField
                fullWidth
                id="name"
                name="name"
                label="Name"
                variant="outlined"
                error={validation.name.error}
                helperText={validation.name.message}
                value={formData.name}
                onChange={handleInputChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                type="email"
                fullWidth
                id="email"
                name="email"
                label="Email"
                variant="outlined"
                error={validation.email.error}
                helperText={validation.email.message}
                value={formData.email}
                onChange={handleInputChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                type="password"
                fullWidth
                id="password"
                name="password"
                label="Password"
                variant="outlined"
                error={validation.password.error}
                helperText={validation.password.message}
                value={formData.password}
                onChange={handleInputChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                type="password"
                fullWidth
                id="confirmPassword"
                name="confirmPassword"
                label="Confirm Password"
                variant="outlined"
                error={validation.confirmPassword.error}
                helperText={validation.confirmPassword.message}
                value={formData.confirmPassword}
                onChange={handleInputChange}
              />
            </Grid>
          </Grid>
          {message && (
            <Alert severity="success" sx={{ my: 2 }}>
              {message}
            </Alert>
          )}
          {err && (
            <Alert severity="error" sx={{ my: 2 }}>
              {err}
            </Alert>
          )}
          <Button
            variant="contained"
            color="primary"
            type="submit"
            fullWidth
            disabled={isLoading}
            sx={{ mt: 3 }}
          >
            {isLoading ? <CircularProgress color="inherit" size={20} /> : "Update Profile"}
          </Button>
        </form>
      </Paper>
    </Container>
  )
}

export default UserProfile
