import React, { useEffect, useState, useContext } from "react"
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Box,
  CircularProgress,
  Alert,
} from "@mui/material"
import { useNavigate } from "react-router-dom"
import { StateContext } from "../shared/context/StateContext"

const loadingStyle = {
  position: "fixed",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "100%",
  textAlign: "center",
}

const MyProductScreen = () => {
  const { userData, token } = useContext(StateContext)
  const [products, setProducts] = useState([])
  const [loading, setLoading] = useState(false)
  const [err, setErr] = useState(null)
  const navigate = useNavigate()
  useEffect(() => {
    if (!userData._id) return
    const fetchData = async () => {
      setLoading(true)
      const response = await fetch(`http://147.139.170.15:80/api/products?userId=${userData._id}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
      })
      if (!response.ok) {
        let text = await response.text()
        setErr(text)
        return
      }
      const data = await response.json()
      setProducts(data)
    }
    fetchData()
  }, [userData._id, token])

  return (
    <>
      {err ? (
        <Alert severity="error">{err}</Alert>
      ) : products.length === 0 ? (
        <Box sx={loadingStyle}>
          <CircularProgress sx={{ mt: 4 }} thickness={7} size={60} />
        </Box>
      ) : (
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="product table">
            <TableHead>
              <TableRow>
                <TableCell>ID</TableCell>
                <TableCell>NAME</TableCell>
                <TableCell>PRICE</TableCell>
                <TableCell>CATEGORY</TableCell>
                <TableCell>BRAND</TableCell>
                <TableCell>ACTIONS</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {products.map((product) => (
                <TableRow key={product._id}>
                  <TableCell>{product._id}</TableCell>
                  <TableCell>{product.name}</TableCell>
                  <TableCell>${product.price}</TableCell>
                  <TableCell>{product.category}</TableCell>
                  <TableCell>{product.brand}</TableCell>
                  <TableCell>
                    <Button
                      onClick={() => {
                        navigate("/add-edit-product", { state: product })
                      }}
                      variant="outlined"
                      color="primary"
                    >
                      Edit
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      {/* Pagination Component */}
      {/* Assuming you have a Paginate component from Material-UI or another library */}
      {/* <Paginate pages={pages} page={page} isAdmin={true} /> */}
    </>
  )
}

export default MyProductScreen
