import React, { useContext } from "react"
import { useNavigate } from "react-router-dom"
import { Box, Button, Container, Divider, Grid, List, Typography } from "@mui/material"
import { Stack } from "@mui/system"
import CartItem from "./component/CartItem"
import { StateContext } from "../shared/context/StateContext"
import { blue } from "@mui/material/colors"

const ListCart = () => {
  const { cartItems, token } = useContext(StateContext)
  const navigate = useNavigate()

  let total = 0
  let totalPrice = 0

  const submitHandler = () => {
    if (token) {
      navigate("/shipping")
    } else {
      navigate("/login?redirect=shipping")
    }
  }

  return (
    <Stack direction={{ xs: "column-reverse", md: "row" }}>
      <Container maxWidth="md">
        <List sx={{ mt: 2 }}>
          {cartItems.map((data) => {
            total += data.qty
            totalPrice += data.qty * data.price
            return <CartItem data={data} key={data._id} />
          })}
        </List>
      </Container>
      <Container maxWidth="xs" sx={{ mt: { xs: 3, md: 0 }, order: { xs: 2, md: 1 } }}>
        <Box
          sx={{
            border: "1px solid",
            borderRadius: 4,
            boxShadow: 4,
            p: 3,
          }}
        >
          <Typography
            variant="h5"
            fontWeight="bold"
            sx={{ textAlign: "center", mb: 2, color: "#333" }}
          >
            SUBTOTAL {total} ITEMS
          </Typography>

          <Divider sx={{ borderBottomWidth: 2, mb: 2 }} />

          <Grid container spacing={2} alignItems="center">
            <Grid item xs={6}>
              <Typography variant="h6" fontWeight="bold" color="#555">
                Total Price:
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <Typography variant="h6" fontWeight="bold" color="#4CAF50">
                ${totalPrice.toFixed(2)}
              </Typography>
            </Grid>
          </Grid>

          <Button
            sx={{
              width: "100%",
              mt: 3,
              backgroundColor: blue[500],
              color: "white",
              "&:hover": { backgroundColor: blue[500] },
            }}
            onClick={submitHandler}
            disabled={total == 0}
          >
            Proceed To Checkout
          </Button>
        </Box>
      </Container>
    </Stack>
  )
}

export default ListCart
